      <c-button
        tag="button"
        class="ml-auto mr-15 px-15 btn-mobile-fixed"
        size="sm"
        variant="outline-info"
        @click="addGroupUser(false)"
      >
        + Add member
      </c-button>
      <c-user-import
        endpoint="USERS_IMPORT"
        @onUploadResponse="onUploadHandler"
        class="btn-import"
      >
        Import
      </c-user-import>
    </div>
    <div class="table-container__loader" v-if="this.showPreload">
      <svg xmlns="http://www.w3.org/2000/svg" style="margin:auto;background:0 0;animation-play-state:running;animation-delay:0s" width="100" height="100" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" display="block" v-bind:svg-inline="''" v-bind:height="'60px'" v-bind:width="'60px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M87.468 36.96A40 40 0 0013.28 66.895 40 42-21.974 0187.468 36.96" fill="#447BE8" style="animation-play-state:running;animation-delay:0s"><animateTransform attributeName="transform" type="rotate" dur="1s" repeatCount="indefinite" keyTimes="0;1" values="0 50 51;360 50 51"/></path></svg>
    </div>
    <span class="btn-show-group-details" @click="$emit('minimizedInfo')">
      <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:width="'25'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><circle cx="15" cy="15" r="15" fill="#2889FC"/><path fill-rule="evenodd" clip-rule="evenodd" d="M7.333 15c0-.368.264-.667.589-.667h8.823c.325 0 .588.299.588.667 0 .368-.263.667-.588.667H7.922c-.325 0-.589-.299-.589-.667z" fill="#fff"/><path fill-rule="evenodd" clip-rule="evenodd" d="M17.138 14.529c.26.26.26.682 0 .942l-4 4a.667.667 0 01-.943-.942l4-4c.26-.26.683-.26.943 0z" fill="#fff"/><path fill-rule="evenodd" clip-rule="evenodd" d="M12.195 10.529c.26-.26.683-.26.943 0l4 4a.667.667 0 01-.943.942l-4-4a.667.667 0 010-.942zM19.333 10.333c.369 0 .667.299.667.667v8a.667.667 0 01-1.333 0v-8c0-.368.298-.667.666-.667z" fill="#fff"/></svg>
    </span>
    <vue-good-table
      class="custom-table p-5 mb-30 custom-table-members"
      :columns="columnsMembers"
      :rows="rowsMembers"
      :search-options="{ enabled: true, placeholder: 'Search' }"
      :pagination-options="{
        enabled: tablesPaginationEnable,
        prevLabel: '',
        nextLabel: '',
      }"
      :sort-options="{
        enabled: true,
        initialSortBy: { field: 'inviteAcceptedOnUtc', type: 'asc' },
      }"
    >
      <template slot="table-row" slot-scope="props">
        <div v-if="props.column.field === 'isAdmin' && (isGroupAdmin || isSystemAdmin)">
          <c-checkbox
            :checked="props.row.isAdmin ? true : false"
            @change="makeAdmin(props.row)"
            type="switcher"
            tag="checkbox"
          >
          </c-checkbox>
        </div>
        <div
          v-else-if="props.column.field === 'action' && (isGroupAdmin || isSystemAdmin)"
        >
          <c-button
            variant="plain"
            class="remove-btn"
            @click="deleteGroupUser(props.row.id, props.row.email)"
            tag="button"
            v-tooltip="'Delete'"
          >
            <svg width="22" height="22" viewBox="0 0 22 22" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:height="'21px'" v-bind:width="'21px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M5.329 4V3c0-1.661 1.427-3 3.178-3h4.263c1.751 0 3.178 1.339 3.178 3v1h4.283c.578 0 1.046.448 1.046 1s-.468 1-1.046 1h-1.085v13c0 1.661-1.428 3-3.178 3H5.31c-1.751 0-3.178-1.339-3.178-3V6H1.046C.468 6 0 5.552 0 5s.468-1 1.046-1H5.33zm2.093 0h6.433V3c0-.548-.481-1-1.085-1H8.507c-.604 0-1.085.452-1.085 1v1zm9.63 2H4.225v13c0 .548.482 1 1.086 1h10.658c.603 0 1.085-.452 1.085-1V6z" fill="#909DB1"/></svg>
          </c-button>
        </div>

        <div v-else-if="props.column.field === 'firstName'" class="user-name-col">
          <div class="user-name-col-image-wrap">
            <span v-if="props.row.photo" class="user-name-col__image">
              <img :src="props.row.photo.url" alt="" />
            </span>
            <span v-else class="user-name-col__image">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 480 480" v-bind:svg-inline="''" v-bind:height="'30px'" v-bind:width="'30px'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M240 0C107.664 0 0 107.664 0 240c0 57.96 20.656 111.184 54.992 152.704.088.12.096.272.192.384 24.792 29.896 55.928 52.816 90.624 67.624.4.168.792.352 1.192.52 2.808 1.184 5.648 2.28 8.496 3.352 1.12.424 2.24.856 3.376 1.264 2.456.88 4.928 1.712 7.416 2.512 1.592.512 3.184 1.016 4.792 1.496 2.2.656 4.408 1.288 6.632 1.888 1.952.528 3.92 1.016 5.888 1.488 1.992.48 3.992.96 6 1.384 2.24.48 4.504.904 6.776 1.32 1.824.336 3.64.688 5.48.984 2.52.408 5.056.728 7.6 1.056 1.64.208 3.272.448 4.92.624 2.88.304 5.784.52 8.696.72 1.352.096 2.696.24 4.056.312 4.248.24 8.544.368 12.872.368s8.624-.128 12.888-.352c1.36-.072 2.704-.216 4.056-.312 2.912-.208 5.816-.416 8.696-.72 1.648-.176 3.28-.416 4.92-.624 2.544-.328 5.08-.648 7.6-1.056 1.832-.296 3.656-.648 5.48-.984 2.264-.416 4.528-.84 6.776-1.32 2.008-.432 4-.904 6-1.384 1.968-.48 3.936-.968 5.888-1.488a232.661 232.661 0 0011.424-3.384c2.488-.8 4.96-1.632 7.416-2.512 1.128-.408 2.248-.84 3.376-1.264a243.168 243.168 0 008.496-3.352c.4-.168.792-.352 1.192-.52a239.997 239.997 0 0090.624-67.624c.096-.112.104-.272.192-.384C459.344 351.184 480 297.96 480 240 480 107.664 372.336 0 240 0zm97.256 441.76c-.12.056-.232.12-.352.176-2.856 1.376-5.76 2.672-8.688 3.936-.664.28-1.32.568-1.984.848a217.023 217.023 0 01-7.76 3.064 159.85 159.85 0 01-3.272 1.192 230.572 230.572 0 01-6.976 2.368c-1.456.464-2.92.904-4.384 1.336a202.08 202.08 0 01-6.28 1.784c-1.776.472-3.568.904-5.36 1.328-1.88.448-3.752.904-5.648 1.304-2.072.44-4.16.816-6.24 1.192-1.688.312-3.368.64-5.072.912-2.344.368-4.712.664-7.072.96-1.496.192-2.984.416-4.496.576-2.696.288-5.416.472-8.128.664-1.208.08-2.408.216-3.632.28-3.96.208-7.928.32-11.912.32s-7.952-.112-11.904-.32c-1.216-.064-2.416-.192-3.632-.28-2.72-.184-5.432-.376-8.128-.664-1.512-.16-3-.384-