            </span>
            <span class="icon-crown" v-if="props.row.isOwner">
              <svg width="20" height="13" viewBox="0 0 20 13" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M1.672 3.948l2.7 7.086A1.5 1.5 0 005.772 12h8.454a1.5 1.5 0 001.402-.966l2.699-7.086c.533-1.399-1.088-2.63-2.293-1.74l-2.513 1.855-2.47-2.43a1.5 1.5 0 00-2.104 0l-2.47 2.43-2.513-1.855c-1.205-.89-2.826.34-2.293 1.74z" fill="#2889FC" stroke="#fff" stroke-width="2"/></svg>
            </span>
          </div>
          <span>{{ props.row.title }}</span>
        </div>
      </template>
    </vue-good-table>
  </div>
</template>
<script>
import { VueGoodTable } from "vue-good-table";
export default {
  components: {
    "vue-good-table": VueGoodTable,
    "c-user-import": () => import("@/components/file-uploader"),
  },
  props: {
    tablesPaginationEnable: {
      default: true,
      type: Boolean,
    },
    showPreload: {
      default: true,
      type: Boolean,
    },
    isGroupAdmin: {
      default: false,
      type: Boolean,
    },
    isSystemAdmin: {
      default: false,
      type: Boolean,
    },
    rowsMembers: {
      default: null,
      type: Array,
    },
  },
  data() {
    return {
      columnsMembers: [
        {
          label: "Name",
          field: "firstName",
        },
        {
          label: "Email",
          field: "email",
        },
        {
          label: "Join Date",
          field: "inviteAcceptedOnUtc",
          sortable: true,
          firstSortType: "desc",
          type: "date",
          dateInputFormat: "MM/dd/yyyy",
          dateOutputFormat: "MM/dd/yyyy",
          thClass: "vgt-left-align th-members-join desktop-only",
          tdClass: "vgt-left-align desktop-only",
        },
        {
          label: "Admin",
          field: "isAdmin",
          type: "boolean",
          sortable: true,
          thClass: "vgt-left-align1330px",
        },
        {
          label: "",
          field: "action",
          filterable: false,
          sortable: false,
        },
      ],
    };
  },
  methods: {
    makeAdmin(row) {
      console.log("makeAdmin", row.id, row.title, row.isAdmin);
      var api = row.isAdmin ? "API_GROUP_CANCEL_ADMIN" : "API_GROUP_MAKE_ADMIN";
      let url = [api, this.$route.params.id, row.id];
      this.setLoadingStatus(true);
      this.makeRequest(url, "POST", null, row.id).then(() => {
        this.setLoadingStatus(false);
        this.$notify({
          group: "system",
          title: "Great!",
          text: "User made as admin!",
          type: "warning",
        });
        this.loadGroup(this.$route.params.id);
      });
    },
    deleteGroupUser(id, text) {
      this.$confirm({
        title: "Are you sure?",
        message: `You want to delete ${text} user!`,
        button: {
          no: "Cancel",
          yes: "Delete",
        },
        callback: (confirm) => {
          if (confirm) {
            let url = ["REMOVE_GROUP_USER", this.$route.params.id, id];
            this.setLoadingStatus(true);
            this.makeRequest(url, "POST", null, id).then(() => {
              this.setLoadingStatus(false);
              this.$notify({
                group: "system",
                title: "Great!",
                text: "User has been removed!",
                type: "warning",
              });
              this.loadGroup(this.$route.params.id);
            });
          }
        },
      });
    },
    onUploadHandler(response) {
      console.log("onUploadHandler", response.data);
      if (response && response.data) {
        var file = response.data.files[0];
        console.log("onUploadHandler files", file);

        this.$store.dispatch("setCurrentFile", file);
        this.$store.dispatch("setActiveModal", "download");
      }
    },
    addGroupUser(admin) {
      let type = admin ? "groupAdmin" : "groupMember";
      this.$store.dispatch("setActiveModal", type);
    },
  },
};
</script>
